import React, { Component } from 'react';
import { Route } from 'react-router-dom';

import SkillsComponent from "../../common/SkillsComponent/SkillsComponent";
import NavbarComponent from '../../common/NavbarComponent/NavbarComponent';
import HeroContentComponent from '../../common/HeroContentComponent/HeroContentComponent';
import SideImageComponent from '../../common/SideImageComponent/SideImageComponent';
import content from '../../configurations/content.json';

class HomePage extends Component {
    state = {
        sideImage: '1.jpg'
    }

    setSideImage = (path) => {
        switch (path) {
            case '/portfolio/':
                this.setState({ sideImage: '1.jpg' });
                break;
            case '/portfolio/about':
                this.setState({ sideImage: '2.jpg' });
                break;
        }
    }

    render() {
        return (
            <div className="container-fluid">
                <div className="row">
                    <div className="col-md-7 container-padding pt-4">
                        <NavbarComponent changeSideImage={(path) => this.setSideImage(path)} />
                        <div className="row mt-4">
                            <HeroContentComponent />
                        </div>
                        <div className="row mt-5">
                            <Route exact path="/portfolio/">
                                <SkillsComponent />
                            </Route>
                            <Route exact path="/portfolio/about">
                                <h2 className="mb-3">Something about me</h2>
                                <h5>
                                    {
                                        content.salutation
                                    }
                                    <br />
                                    {
                                        content.about
                                    }
                                </h5>
                                <h2 className="mt-3 mb-1">Clients I've worked with</h2>
                                <div className="row">
                                    {
                                        content.clients.map((client, index) => {
                                            return (
                                                <div className="col-md-4">
                                                    <img style={{ objectFit: 'contain', width: '50%' }} className="img img-responsive"
                                                        src={require(`../../assets/images/${client.logoName}`).default} />
                                                    <p className="description">
                                                        {
                                                            client.description
                                                        }
                                                    </p>
                                                </div>
                                            );
                                        })
                                    }
                                </div>
                            </Route>
                        </div>
                    </div>
                    <div className="col-md-5 no-padding  d-none d-xs-none d-sm-none d-md-block">
                        <SideImageComponent currentImage={this.state.sideImage} />
                    </div>
                </div>
            </div>
        );
    }
}

export default HomePage;